require('../functions/vect');

let GpuInstance;

export default class Gpu {
    constructor() {
        this._gpujs = new GPU({mode: 'webgl2'});

        this._gpujs.addFunction(vecAdd, {returnType: 'Array(3)'});
        this._gpujs.addFunction(vecSub, {returnType: 'Array(3)'});
        this._gpujs.addFunction(vecScale, {returnType: 'Array(3)'});
        this._gpujs.addFunction(vecCross, {returnType: 'Array(3)'});
        this._gpujs.addFunction(vecDot, {returnType: 'Number'});
        this._gpujs.addFunction(vecUnit, {returnType: 'Array(3)'});
        this._gpujs.addFunction(vecLength, {returnType: 'Number'});
        this._gpujs.addFunction(vecReflect, {returnType: 'Array(3)'});
    }

    static makeKernel(fn) {
        GpuInstance = GpuInstance || new Gpu();
        return GpuInstance._gpujs.createKernel(fn);
    }
}